<template>
  <v-container>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <v-btn @click="generatePDF()" color="primary">Generate PDF</v-btn>

    <vue-html2pdf
      :show-layout="false"
      :float-layout="true"
      :enable-download="false"
      :preview-modal="true"
      :paginate-elements-by-height="3000"
      filename="maCaisse"
      :pdf-quality="2"
      :manual-pagination="false"
      pdf-format="a4"
      :pdf-margin="5"
      pdf-orientation="portrait"
      pdf-content-width="800px"
      @progress="onProgress($event)"
      ref="html2Pdf"
    >
      <section slot="pdf-content">
        <ContentToPrint />
      </section>
    </vue-html2pdf>
    <div>
      <ContentToPrint />
    </div>
  </v-container>
</template>

<script>
import VueHtml2pdf from "vue-html2pdf";
import ContentToPrint from "../components/ContentToPrint.vue";

export default {
  data() {
    return {
      overlay: false,
    };
  },
  name: "App",
  methods: {
    onProgress(event) {
      this.overlay = true;
      if (event == 100) {
        this.overlay = false;
      }
      console.log(`Processed: ${event} / 100`);
    },
    hasGenerated() {
      alert("PDF generated successfully!");
    },
    generatePDF() {
      this.$refs.html2Pdf.generatePdf();
    },
  },
  components: {
    VueHtml2pdf,
    ContentToPrint,
  },
};
</script>